          se les llama Nu nachak, que significa "nuestro gran fogn", o Gran
          Confederacin Misak, y se refiere a la unin social Misak como si
          fuese un fogn familiar de las antiguas cocinas. De estos fogones es
          de donde tradicionalmente nace todo: la palabra, la historia, el
          territorio, la familia y la autoridad. Han sido reconocidos los
          siguientes resguardos Misak: Guamba (Silvia, Cauca) La Mara
          (Piendam, Cauca) Bonanza (Morales, Cauca) La Gaitana (La Plata,
          Huila) Nuevo Amanecer (La Argentina, Huila) Nam Misak (La Plata y La
          Argentina, Huila) Comparten con la comunidad nasa el resguardo de La
          Reforma, en La Plata. En Tulu, se encuentra una comunidad en Barragn
