          (Cajibo), Piscitao, (Pinedam), Munchique (El Tambo), San Antonio
          (Morales) y Ovejas (Caldono)
        </p>
        <h3>Aspectos culturales</h3>
        <p>
          Los grupos domsticos patrilocales son la base de la organizacin
          social. Practican la endogamia tnica y la exogamia veredal (de la
          comunidad local). La base de su economa es la agricultura Sus
          principales cultivos son el maz, papa, caf, ulluco, frijol, repollo
          y habas. Su pensamiento es dual, basado en oposiciones como sol-luna,
          masculino-femenino. El mrpik es el curandero Misak, es escogido
          entre nios con disposicin y educado para intermediar con los
          espritus. Realiza la ceremonia de limpieza llamada pishimarp. El
          traje tradicional del hombre es una reboso corta azul que envuelven
          alrededor de la cintura, dos ruanas rectangulares y sombrero. Las
          mujeres llevan un anaco vestimenta propia de los misak, blusa de un
          solo color y paoln azul de lana.
