var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('div',[_c('MenuInicial')],1),_c('div',{staticClass:"fondo"}),_c('div',[_c('section',[_c('H2',[_vm._v("Nuestro Equipo")]),_c('hr'),_c('p',[_vm._v(" Los misak o guambianos son un pueblo originario americano que habita en el sur de Colombia en el departamento del Cauca. Su Resguardo Mayor original Guamba est en el municipio de Silvia y habitan tambin en otros lugares cercanos, de la Cordillera Central de los Andes colombianos. Para poder acceder a tierra cultivable, algunos han emigrado a fundar nuevos resguardos en el mismo Cauca. Para los antroplogos, los Misak son originarios del territorio del prehispnico cacicazgo de Calambs, que haca parte de la Confederacin Pubense, cuyo centro era la actual Popayn, en el Valle de Pubenza y que llegaba hasta Santander de Quilichao al norte, al sur el ro Moras, y al oriente los pramos y las estribaciones orientales de la Cordillera Central. Con la Conquista y la Colonia este cacicazgo desapareci, y aquellos que se refugiaron en las estribaciones ms lejanas de los pramos, pudieron pervivir. Con las dcadas recibiran a otros Misak que huan de las zonas de tierra caliente. De esta mezcla de poblaciones vienen los actuales Misak. La Corona espaola titul en 1700 como resguardo a Guamba, cuya autoridad principal es el Cabildo, elegido actualmente por voto popular. ")]),_c('h3',[_vm._v("Misin")]),_c('p',[_vm._v(" Con los siglos muchos Misak migraran de nuevo a tierra caliente, y a partir de 1991 con el cambio Constitucional, pudieron conformar nuevos resguardos. A toda la red de nuevos resguardos (16 y dos comunidades), se les llama Nu nachak, que significa \"nuestro gran fogn\", o Gran Confederacin Misak, y se refiere a la unin social Misak como si fuese un fogn familiar de las antiguas cocinas. De estos fogones es de donde tradicionalmente nace todo: la palabra, la historia, el territorio, la familia y la autoridad. Han sido reconocidos los siguientes resguardos Misak: Guamba (Silvia, Cauca) La Mara (Piendam, Cauca) Bonanza (Morales, Cauca) La Gaitana (La Plata, Huila) Nuevo Amanecer (La Argentina, Huila) Nam Misak (La Plata y La Argentina, Huila) Comparten con la comunidad nasa el resguardo de La Reforma, en La Plata. En Tulu, se encuentra una comunidad en Barragn y en Cali, vive hace 50 aos la comunidad Nu Pachik chak. Otros asentamientos Misak estn en territorios del Cauca, como Kurak-Chak (Cajibo), Piscitao, (Pinedam), Munchique (El Tambo), San Antonio (Morales) y Ovejas (Caldono) ")]),_c('h3',[_vm._v("Visin")]),_c('p',[_vm._v(" Los grupos domsticos patrilocales son la base de la organizacin social. Practican la endogamia tnica y la exogamia veredal (de la comunidad local). La base de su economa es la agricultura Sus principales cultivos son el maz, papa, caf, ulluco, frijol, repollo y habas. Su pensamiento es dual, basado en oposiciones como sol-luna, masculino-femenino. El mrpik es el curandero Misak, es escogido entre nios con disposicin y educado para intermediar con los espritus. Realiza la ceremonia de limpieza llamada pishimarp. El traje tradicional del hombre es una reboso corta azul que envuelven alrededor de la cintura, dos ruanas rectangulares y sombrero. Las mujeres llevan un anaco vestimenta propia de los misak, blusa de un solo color y paoln azul de lana. ")]),_c('h4',[_vm._v("Slogan")]),_c('p',[_vm._v("La base de su economa es la agricultura Sus principales cultivos")]),_c('h4',[_vm._v("Logo")]),_c('br'),_vm._m(0)],1)]),_c('div',{staticClass:"footer"},[_c('Footer')],1)])}
